import { FastColor } from '@ant-design/fast-color';
const hueStep = 2; // è‰²ç›¸é˜¶æ¢¯
const saturationStep = 0.16; // é¥±å’Œåº¦é˜¶æ¢¯ï¼Œæµ…è‰²éƒ¨åˆ†
const saturationStep2 = 0.05; // é¥±å’Œåº¦é˜¶æ¢¯ï¼Œæ·±è‰²éƒ¨åˆ†
const brightnessStep1 = 0.05; // äº®åº¦é˜¶æ¢¯ï¼Œæµ…è‰²éƒ¨åˆ†
const brightnessStep2 = 0.15; // äº®åº¦é˜¶æ¢¯ï¼Œæ·±è‰²éƒ¨åˆ†
const lightColorCount = 5; // æµ…è‰²æ•°é‡ï¼Œä¸»è‰²ä¸Š
const darkColorCount = 4; // æ·±è‰²æ•°é‡ï¼Œä¸»è‰²ä¸‹

// æš—è‰²ä¸»é¢˜é¢œè‰²æ˜ å°„å…³ç³»è¡¨
const darkColorMap = [{
  index: 7,
  amount: 15
}, {
  index: 6,
  amount: 25
}, {
  index: 5,
  amount: 30
}, {
  index: 5,
  amount: 45
}, {
  index: 5,
  amount: 65
}, {
  index: 5,
  amount: 85
}, {
  index: 4,
  amount: 90
}, {
  index: 3,
  amount: 95
}, {
  index: 2,
  amount: 97
}, {
  index: 1,
  amount: 98
}];
function getHue(hsv, i, light) {
  let hue;
  // æ ¹æ®è‰²ç›¸ä¸åŒï¼Œè‰²ç›¸è½¬å‘ä¸åŒ
  if (Math.round(hsv.h) >= 60 && Math.round(hsv.h) <= 240) {
    hue = light ? Math.round(hsv.h) - hueStep * i : Math.round(hsv.h) + hueStep * i;
  } else {
    hue = light ? Math.round(hsv.h) + hueStep * i : Math.round(hsv.h) - hueStep * i;
  }
  if (hue < 0) {
    hue += 360;
  } else if (hue >= 360) {
    hue -= 360;
  }
  return hue;
}
function getSaturation(hsv, i, light) {
  // grey color don't change saturation
  if (hsv.h === 0 && hsv.s === 0) {
    return hsv.s;
  }
  let saturation;
  if (light) {
    saturation = hsv.s - saturationStep * i;
  } else if (i === darkColorCount) {
    saturation = hsv.s + saturationStep;
  } else {
    saturation = hsv.s + saturationStep2 * i;
  }
  // è¾¹ç•Œå€¼ä¿®æ­£
  if (saturation > 1) {
    saturation = 1;
  }
  // ç¬¬ä¸€æ ¼çš„ s é™åˆ¶åœ¨ 0.06-0.1 ä¹‹é—´
  if (light && i === lightColorCount && saturation > 0.1) {
    saturation = 0.1;
  }
  if (saturation < 0.06) {
    saturation = 0.06;
  }
  return Math.round(saturation * 100) / 100;
}
function getValue(hsv, i, light) {
  let value;
  if (light) {
    value = hsv.v + brightnessStep1 * i;
  } else {
    value = hsv.v - brightnessStep2 * i;
  }
  // Clamp value between 0 and 1
  value = Math.max(0, Math.min(1, value));
  return Math.round(value * 100) / 100;
}
export default function generate(color, opts = {}) {
  const patterns = [];
  const pColor = new FastColor(color);
  const hsv = pColor.toHsv();
  for (let i = lightColorCount; i > 0; i -= 1) {
    const c = new FastColor({
      h: getHue(hsv, i, true),
      s: getSaturation(hsv, i, true),
      v: getValue(hsv, i, true)
    });
    patterns.push(c);
  }
  patterns.push(pColor);
  for (let i = 1; i <= darkColorCount; i += 1) {
    const c = new FastColor({
      h: getHue(hsv, i),
      s: getSaturation(hsv, i),
      v: getValue(hsv, i)
    });
    patterns.push(c);
  }

  // dark theme patterns
  if (opts.theme === 'dark') {
    return darkColorMap.map(({
      index,
      amount
    }) => new FastColor(opts.backgroundColor || '#141414').mix(patterns[index], amount).toHexString());
  }
  return patterns.map(c => c.toHexString());
}